
import { BrowserModule } from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import {MatMenuModule} from '@angular/material/menu';
import {MatToolbarModule} from '@angular/material/toolbar'; 
import {MatButtonModule} from '@angular/material/button';
import {MatIconModule} from '@angular/material/icon';
import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { ConnectComponent } from './connect/connect.component';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './prime-dashboard/dashboard.component';
import { NgxKerviModule } from 'ngx-kervi';
import { NGXKerviPipesModule } from 'ngx-kervi'
import { NgxKerviComponentsModule  } from 'ngx-kervi-components'
import { KerviMaterialModule  } from 'kervi-material'
import { FlexLayoutModule } from '@angular/flex-layout';
const ROUTES: Routes = [
  
  { path: 'connect', component: ConnectComponent },
  { path: 'authenticate', component: LoginComponent },
  { path: 'dashboard/:name', component: DashboardComponent },
  {
    path: '',
    redirectTo: '/connect',
    pathMatch: 'full'
  }
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    ConnectComponent,
    DashboardComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MatMenuModule,
    MatToolbarModule,
    MatButtonModule,
    MatIconModule,
    NgxKerviModule,
    NGXKerviPipesModule,
    KerviMaterialModule,
    FlexLayoutModule,
    //NgxKerviComponentsModule,
    RouterModule.forRoot(ROUTES)
  ],
  
  bootstrap: [AppComponent]
})
export class AppModule { }
